import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow} from "mdbreact";
import Step from "./Step";
import {fieldValidator} from "./validator";

export default function Step1(props) {
    const {state, nextStep} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        switch (fieldName) {
            case 'reqFor':
                if (value === "Spouse") {
                    updatedState.reqForSub = "";
                    updatedState.reqFor = value;
                } else {
                    updatedState.reqFor = value;
                }
                setErrorMsg(fieldValidator(fieldName, value, 'checkbox', errorMsg).error);
                break;
            case 'reqForSub':
                updatedState.reqForSub = value;
                updatedState.adoptRelated = null;
                setErrorMsg(fieldValidator(fieldName, value, 'checkbox', errorMsg).error);
                break;
            case 'adoptRelated':
                updatedState.adoptRelated = value;
                updatedState.reqForSub = "";
                break;
            case 'adoptCiti':
                updatedState.adoptCiti = value;
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };

    const onNext = () => {
        if (state.reqFor === "Spouse") {
            props.history.push(nextStep);
        } else {
            let errors = fieldValidator('reqFor', state.reqFor, 'checkbox').error;
            let newForm = fieldValidator('reqForSub', state.reqForSub, 'checkbox', errors);

            if (newForm.isValid) {
                props.history.push(nextStep);
            } else {
                setErrorMsg(newForm.error);
            }
        }
    };

    return (
        <Step
            heading={
                "Who are you completing this request for?"
            }
        >
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`12`}>
                        <MDBRow center>
                            <MDBFormInline>
                                <MDBInput
                                    label='Spouse'
                                    type='checkbox'
                                    name={`relationQue`}
                                    id='spouse'
                                    checked={state.reqFor === "Spouse"}
                                    containerClass='mr-5'
                                    className={`custom-check-large`}
                                    onChange={e => {
                                        if (e.target.checked) {
                                            onChange(e, "reqFor", "Spouse");
                                        }
                                    }}
                                />
                                <MDBInput
                                    label='Parent'
                                    type='checkbox'
                                    id='parent'
                                    name={`relationQue`}
                                    checked={state.reqFor === "Parent"}
                                    className={`custom-check-large`}
                                    containerClass='mr-5'
                                    onChange={e => {
                                        if (e.target.checked) {
                                            onChange(e, "reqFor", "Parent");
                                        }
                                    }}
                                />
                                <MDBInput
                                    label='Brother/Sister'
                                    type='checkbox'
                                    id='brother'
                                    name={`relationQue`}
                                    checked={state.reqFor === "Brother"}
                                    className={`custom-check-large`}
                                    containerClass='mr-5'
                                    onChange={e => {
                                        if (e.target.checked) {
                                            onChange(e, "reqFor", "Brother");
                                        }
                                    }}
                                />
                                <MDBInput
                                    label='Child'
                                    type='checkbox'
                                    id='child'
                                    name={`relationQue`}
                                    checked={state.reqFor === "Child"}
                                    className={`custom-check-large`}
                                    containerClass='mr-5'
                                    onChange={e => {
                                        if (e.target.checked) {
                                            onChange(e, "reqFor", "Child");
                                        }
                                    }}
                                />
                            </MDBFormInline>
                        </MDBRow>

                        {(state.reqFor === "Parent" || state.reqFor === "Child") && <React.Fragment>
                            <div>
                                <h3 className={`text-center`}>
                                    What best Describes your Relationship?
                                </h3>
                            </div>

                            <MDBRow center>
                                <div className={`text-center`}>
                                    <MDBRow start>
                                        <MDBInput
                                            label='Child was born to parents who were married to each other at the time of the child`s birth'
                                            type='checkbox'
                                            id='parentSub1'
                                            checked={state.reqForSub === "sub1"}
                                            size={'sm'}
                                            containerClass='mr-5 mb-2 d-flex align-items-center'
                                            className={`custom-check-large`}
                                            labelClass={`ml-2`}
                                            onChange={e => {
                                                if (e.target.checked) {
                                                    onChange(e, "reqForSub", "sub1");
                                                }
                                            }}
                                        />
                                    </MDBRow>
                                    <MDBRow start>
                                        <MDBInput
                                            label='Stepchild/Stepparent'
                                            type='checkbox'
                                            id='parentSub2'
                                            name={`relationSubQue`}
                                            checked={state.reqForSub === "sub2"}
                                            size={'lg'}
                                            containerClass='mr-5 mb-2 d-flex align-items-center'
                                            className={`custom-check-large`}
                                            labelClass={`ml-2`}
                                            onChange={e => {
                                                if (e.target.checked) {
                                                    onChange(e, "reqForSub", "sub2");
                                                }
                                            }}
                                        />
                                    </MDBRow>
                                    <MDBRow start>
                                        <MDBInput
                                            label='Child was born to parents who were not married to each other at the time of the child`s birth'
                                            type='checkbox'
                                            id='parentSub3'
                                            name={`relationSubQue`}
                                            checked={state.reqForSub === "sub3"}
                                            size={'lg'}
                                            containerClass='mr-5 mb-2 d-flex align-items-center'
                                            className={`custom-check-large`}
                                            labelClass={`ml-2`}
                                            onChange={e => {
                                                if (e.target.checked) {
                                                    onChange(e, "reqForSub", "sub3");
                                                }
                                            }}
                                        />
                                    </MDBRow>
                                    <MDBRow start>
                                        <MDBInput
                                            label='Child was adopted (not an Orphan or Hague Convention adoptee)'
                                            type='checkbox'
                                            id='parentSub4'
                                            checked={state.reqForSub === "sub4"}
                                            size={'lg'}
                                            name={`relationSubQue`}
                                            containerClass='mr-5 mb-2 d-flex align-items-center'
                                            className={`custom-check-large`}
                                            labelClass={`ml-2`}
                                            onChange={e => {
                                                if (e.target.checked) {
                                                    onChange(e, "reqForSub", "sub4");
                                                }
                                            }}
                                        />
                                    </MDBRow>
                                </div>
                            </MDBRow>

                        </React.Fragment>}

                        {(state.reqFor === "Brother") && <React.Fragment>
                            <div>
                                <h3 className={`text-center`}>
                                    Are you related by Adoption?
                                </h3>
                            </div>

                            <MDBRow center>
                                <MDBFormInline>
                                    <MDBInput
                                        label='Yes'
                                        type='checkbox'
                                        id='yes'
                                        name={`adoptionBool1`}
                                        checked={state.adoptRelated}
                                        className={`custom-check-large`}
                                        containerClass='mr-5'
                                        onChange={e => {
                                            if (e.target.checked) {
                                                onChange(e, "adoptRelated", true);
                                            }
                                        }}
                                    />
                                    <MDBInput
                                        label='No'
                                        type='checkbox'
                                        id='no'
                                        name={`adoptionBool2`}
                                        checked={state.adoptRelated === false}
                                        className={`custom-check-large`}
                                        containerClass='mr-5'
                                        onChange={e => {
                                            if (e.target.checked) {
                                                onChange(e, "adoptRelated", false);
                                            }
                                        }}
                                    />
                                </MDBFormInline>
                            </MDBRow>
                        </React.Fragment>}

                        {state.reqFor && <React.Fragment>
                            <div>
                                <h3 className={`text-center`}>
                                    Did you gain resident status or citizenship through adoption?
                                </h3>
                            </div>

                            <MDBRow center>
                                <MDBFormInline>
                                    <MDBInput
                                        label='Yes'
                                        type='checkbox'
                                        id='yesCiti1'
                                        name={`adoptionCiti1`}
                                        checked={state.adoptCiti}
                                        className={`custom-check-large`}
                                        containerClass='mr-5'
                                        onChange={e => {
                                            if (e.target.checked) {
                                                onChange(e, "adoptCiti", true);
                                            }
                                        }}
                                    />
                                    <MDBInput
                                        label='No'
                                        type='checkbox'
                                        id='noCity2'
                                        name={`adoptCiti2`}
                                        checked={state.adoptCiti === false}
                                        className={`custom-check-large`}
                                        containerClass='mr-5'
                                        onChange={e => {
                                            if (e.target.checked) {
                                                onChange(e, "adoptCiti", false);
                                            }
                                        }}
                                    />
                                </MDBFormInline>
                            </MDBRow>
                        </React.Fragment>}


                        <MDBRow center className="d-flex flex-column align-items-center">
                            {(errorMsg.msg) && (
                                <div className="text-danger mb-2">{errorMsg.msg}</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Let's Do This
                            </MDBBtn>
                        </MDBRow>

                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
